let name;
let activator = "open-modal";
let closeModal = "close-modal";
let locate = "modal";
let currentModal;

export default (e) => {
  const modalTrigger = e.target.closest(`[${activator}]`);

  if (modalTrigger) {
    name = modalTrigger.getAttribute(activator);
    if (!name) return;

    const modals = document.querySelectorAll(`[${locate}="${name}"].b-modal`);
    
    if (modals.length === 1) {
      currentModal = modals[0];
      currentModal.classList.add("opened");
      // observeClosedClass(currentModal);
    } else if (modals.length > 1) {
      console.error(`Se han encontrado ${modals.length} modales con el mismo valor "${name}"`);
    } else {
      console.error(`No se ha encontrado ninguna modal con: ${locate}="${name}"`);
    }

  } else if (name && currentModal) {
    handleModalEvents(e);
  }
};

function handleModalEvents(e) {
  const outsideModal = !e.target.closest(`[${locate}="${name}"].b-modal [bx-content]`);
  const onCloseModal = e.target.closest(`[${locate}].b-modal [bx-content] [${closeModal}]`);
  const maximizeModal = e.target.closest(`[${locate}].b-modal [bx-content] [maximize-modal]`);
  const isPersistent = e.target.closest(`[${locate}="${name}"].b-modal[persistent]`);

  window.addEventListener("keydown", (ev) => {
    if (ev.key === "Escape" && currentModal?.classList.contains("opened")) {
      closeModalHandler();
    }
  }, { once: true });

  const shouldClose =
    (onCloseModal && outsideModal && !isPersistent) ||
    onCloseModal ||
    (currentModal && outsideModal && !isPersistent);

  if (shouldClose) {
    closeModalHandler();
  }

  if (currentModal && maximizeModal) {
    currentModal.classList.toggle("maximized");
  }
}

function closeModalHandler() {
  closePopupScreen(name);
  name = null;
  currentModal = null;
}

export async function closePopupScreen(name) {
  const modal = document.querySelector(`.b-modal[modal="${name}"]`);
  if (!modal) return false;

  modal.classList.add("closed");

  await new Promise((resolve) => {
    setTimeout(() => {
      modal.classList.remove("opened");
      modal.classList.remove("closed");
      resolve();
    }, 400);
  });

  return true;
}
